.container_item-left{
    height: 500px;
    flex-basis:20%;
    /*border:1px solid gray;*/
    display:flex;
    flex-wrap: wrap;
    overflow-y: scroll;
    padding: 1%;
    min-width: 160px;
    background:linear-gradient(rgba(44, 46, 47, 0.62), rgba(5, 18, 40, 0.62));
    color: rgb(12, 99, 214)
}
.avatar{
    width:50px;
    height: 50px;
    border-radius: 100%;
    margin-right: 10px
}
.after_image{
    display: inline-block;
}
.image{
    width:50px;
    height: 50px;
    border-radius: 100%;
    border:1px solid rgb(216, 231, 240);
    display: inline-block
}